/* basic defaults for theme styling */
body {
  margin: 0;
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

a#main-content {
  /* anchor link to main content has no height */
  height: 0px;
}

body * {
  box-sizing: border-box; /* padding and margin won't effect the width */
}

#container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  align-items: center;
}

.pagep {
  /* set padding for elements that set the site width */
  padding: 0 1.568em;
}

@media all and (min-width: 768px) {
  /* set for desktop screens */
  .pagep {
    padding: 0 7.568em;
  }
}
/* main regions */
.page-header,
.page-main,
.page-footer {
  width: 100%;
}

.page-main {
  flex-grow: 1;
}

/********************/
/* site breakpoints */
/********************/
/* handheld */
@media all and (max-width: 767px) {
  html {
    font-size: 2.6vw;
  }
  .sitew {
    width: 100vw;
  }
  /* /handheld */
}
/* desktop - small - 768 - 1024 */
@media all and (max-width: 1023px) and (min-width: 768px) {
  html {
    font-size: 8.8px; /* 40% */
  }
  .sitew {
    width: 768px;
  }
  /* /small */
}
/* desktop - medium - 1024 - 1366 */
@media all and (max-width: 1364px) and (min-width: 1024px) {
  html {
    font-size: 11.7333333333px; /* 53.333333333333336% */
  }
  .sitew {
    width: 1024px;
  }
  /* /medium */
}
/* desktop -large - 1366+ */
@media all and (max-width: 1919px) and (min-width: 1365px) {
  html {
    font-size: 15.640625px; /* 71.09375% */
  }
  .sitew {
    width: 1365px;
  }
  /* /large */
}
/* desktop - max - 1920+ */
@media all and (min-width: 1920px) {
  html {
    font-size: 22px; /* 100% - all media query body font-size math starts here */
  }
  .sitew {
    width: 1920px;
  }
  /* /max */
}
/********************/
/* /site breakpoints */
/********************/
/**
* Responsive tables.
*/
@media all and (max-width: 767px) {
  /* handheld */
  th.priority-low,
  td.priority-low,
  th.priority-medium,
  td.priority-medium {
    display: none;
  }
}
@media all and (max-width: 1023px) and (min-width: 768px) {
  /* small 768px - 1024p */
  th.priority-low,
  td.priority-low {
    display: none;
  }
}
